// Responsive Embeds
.embed-responsive {
  position: relative;
  display: block;
  height: 0;
  padding: 0;
  overflow: hidden;
}
.embed-responsive .embed-responsive-item,
.embed-responsive iframe,
.embed-responsive embed,
.embed-responsive object,
.embed-responsive video {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  border: 0;
}
.embed-responsive-16by9 {
  padding-bottom: 56.25%;
}
.embed-responsive-4by3 {
  padding-bottom: 75%;
}

// Global Layout
.block-system-main-block {
  clear: both;
}

.region-content {
  padding: 0;

  .block-system-main-block {
    padding: 0 16px;
  }
}

.region-sidebar + .region-content {
  padding: 0 16px;
  @include bp(medium) {
    float: left;
    width: 75%;
  }
}

.region-sidebar {
  display: none;
  @include bp(medium) {
    float: right;
    width: 25%;
    display: block;
  }
}

.feed-icons {
  clear: both;
  display: block;
  padding: 16px;
}
