// custom fonts and fonticons
@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon.eot?em6kwp');
  src:  url('../fonts/icomoon.eot?em6kwp#iefix') format('embedded-opentype'),
    url('../fonts/icomoon.ttf?em6kwp') format('truetype'),
    url('../fonts/icomoon.woff?em6kwp') format('woff'),
    url('../fonts/icomoon.svg?em6kwp#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
